export default function (s) {
    s.state = {}
    s.dispatch = () => { }

    // Variables
    let hp;
    let thresh, maxitr;
    let xmin, xmax, ymin, ymax;
    let r, theta, c1, c2, c3, c4;
    let vals = [-1.2, 1.2, -1.2, 1.2];
    let ms, ss, vs;
    let minval, maxval;

    // functions
    let iterate_julia = (a, b)=> {
        let i = 0;
        while (i < maxitr) {
            const fa = a * a - b * b + c1;
            b = 2 * a * b + c2;
            a = fa;
            i += 1;
            if (a * a + b * b > thresh) {
                return i;
            }
            i++;
        }
    }

    let gcd = (a, b) => {
        if (a <= 1 || b <= 1) {
            return 1;
        } else if (a % b == 0) {
            return b;
        } else return gcd(b, a % b);
    }

    let resetImage = () => {
        const { hash } = s.state;
        console.log(hash);
        hp = [];
        for (let i = 2; i < hash.length; i = i + 2) {
            const val = parseInt('0x' + hash.substring(i, i + 2));
            console.log(i, val);
            hp.push(val);
        }
        // Indices of parameters
        // 0 : r
        // 1 : theta
        r = 0.7855 + s.map(hp[0], 0, 256, -0.1, + 0.1);
        theta = s.map(hp[2], 0, 256, 0, 2*s.PI);
        c1 = r * s.cos(theta);
        c2 = r * s.sin(theta);

        // 2, 3, 4 : Control options to select algorithm
        maxitr = 3 * hp[4] + 8;
        thresh = hp[2] * hp[3] + 3;
        /*
        thresh = pow(1.44, threshslider.value());
        maxitr = maxitrslider.value();
        c1 = -0.75;
        c2 = 0.11;*/

        // 5, 6 : Select x, y ranges
        xmin = vals[0] - s.map(hp[5], 0, 256, 0, 0.5);
        xmax = vals[1] + s.map(hp[5], 0, 256, 0, 0.5);
        ymin = vals[2] - s.map(hp[6], 0, 256, 0, 0.5);
        ymax = vals[3] + s.map(hp[6], 0, 256, 0, 0.5);

        // 7, 8 : Underlying mandelbrot
        // 9 : Modulus for color selection
        // 10, 11, 12 : r, g, b
        // 13, 14, 15 : color scale

        // mr = hp[10] / gcd(hp[9], hp[10]);
        // mg = hp[11] / gcd(hp[9], hp[11]);
        // mb = hp[12] / gcd(hp[9], hp[12]);
        ms = [];
        ss = [];
        vs = [];
        for (let k = 0; k < 3; k++) {
            const m = gcd(hp[9], hp[10 + k]);
            ms.push(m == 1 ? hp[9] : m);
            ss.push(m == 1 ? hp[13 + k] % 2 : m % 2);
            vs.push(s.map(hp[13 + k], 0, 256, 0, 1));
        }

        // Other constants
        minval = 255;
        maxval = 0;
    };

    s.setup = () => {
        const { dim } = s.state;
        console.log(dim);
        s.createCanvas(dim, dim);
        hp = [];

        //blendMode(DIFFERENCE);
        console.log("Setup");
    }

    s.draw = () => {
        if (s.state.refresh) {
            resetImage();
            s.pixelDensity(1);
            s.loadPixels();
            // use hp3 for a mapping

            console.log(r, theta, c1, c2, s.width, s.height);
            for (let i = 0; i < s.width; i++) {
                for (let j = 0; j < s.height; j++) {
                    let pix = (i + j * s.width) * 4;
                    c3 = s.map(i, 0, s.height, xmin, xmax);
                    c4 = s.map(j, 0, s.width, ymin, ymax);
                    let grad = iterate_julia(c3, c4);
                    let val = s.map(grad, 0, maxitr, 0, 256);
                    // Calculate Julia
                    let cols = (maxitr - grad) * (i * i + j * j);
                    for (let k = 0; k < 3; k++) {
                        s.pixels[pix + k] = vs[k] * val + s.map((ss[k] * cols) % ms[k], 0, ms[k], 0, 255);
                        if (s.pixels[pix + k] > maxval) maxval = s.pixels[pix + k];
                        if (s.pixels[pix + k] < minval) minval = s.pixels[pix + k];
                    }
                    s.pixels[pix + 3] = 255;
                }
            }
            console.log("Rescaling", minval, maxval);
            // Normalize
            for (let i = 0; i < s.width; i++) {
                for (let j = 0; j < s.height; j++) {
                    let pix = (i + j * s.width) * 4;
                    for (let k = 0; k < 3; k++) {
                        s.pixels[pix + k] = s.map(s.pixels[pix + k], minval, maxval, 0, 255);
                    }
                }
            }

            s.updatePixels();
            console.log("Redraw finished");
            s.dispatch(
                {
                    type: 'SET_VALUE',
                    payload: {
                        key: 'refresh',
                        value: false,
                    },
                }
            );
            const canv = s.get();
            console.log(canv);
            s.dispatch(
                {
                    type: 'SET_VALUE',
                    payload: {
                        key: 'canvas',
                        value: canv.canvas,
                    },
                }
            );
        }
        
    }

    s.saveArt = () => {

        
    }
}
